import { InputGroup, FormControl } from 'react-bootstrap';

const SecurityBtn = ({ securities, authType, setAuthType, setAuthHeader }) => {
  return(
    <>
      {
        securities
        ?
        <div>
          <select onChange={e => setAuthType(e.target.value)}>
            { securities.map(securityObj => {
              const security = Object.keys(securityObj)[0];
              return <option key={security}>{security}</option>;
              })
            }
          </select>
          {
            (()=>{
            switch (authType) {
              case 'basicAuth':
                return <BasicAuthBtn setAuthHeader={setAuthHeader} />;
              case 'bearerAuth':
                return <BearerAuthBtn setAuthHeader={setAuthHeader}/>;
              case 'ApiKeyAuth':
                return <ApiKeyAuthBtn setAuthHeader={setAuthHeader}/>;
              case 'cookieAuth':
                return <></>;
              case 'oAuthSample':
                return <></>;
              case 'openId':
                return <></>;
              default:
                return <></>;
              }
            })()
          }
        </div>
        :
        null
      }
     </>
  );
};

const BasicAuthBtn = ({ setAuthHeader }) => {

  function handleUserName(userName) {
    setAuthHeader(authHeader => {
      const newAuthHeader = {...authHeader};
      newAuthHeader.key = 'Auth';
      newAuthHeader.value.username = userName;
      return newAuthHeader;
    });
  }

  function handleUserPwd(password) {
    setAuthHeader(authHeader => {
      const newAuthHeader = {...authHeader};
      newAuthHeader.key = 'Auth';
      newAuthHeader.value.password = password;
      return newAuthHeader;
    });
  }

  return (
    <>
      <InputGroup className="mb-3">
        <InputGroup.Text>Username and Password</InputGroup.Text>
        <FormControl
          aria-label="Username"
          placeholder="Username"
          onChange={(e) => handleUserName(e.target.value)}
          />
        <FormControl
          aria-label="Password"
          placeholder="Password"
          onChange={(e) => handleUserPwd(e.target.value)}
          />
      </InputGroup>
    </>
  );
};

const BearerAuthBtn = ({ setAuthHeader }) => {
  return (
    <>
      <InputGroup className="mb-3">
        <InputGroup.Text id="bearer-token">Bearer</InputGroup.Text>
        <FormControl
          placeholder="token"
          aria-label="token"
          aria-describedby="bearer-token"
          onChange={(e) => setAuthHeader({key: 'Authorization', value: `Bearer ${e.target.value}`})}
        />
      </InputGroup>
    </>
  );
};

const ApiKeyAuthBtn = ({ setAuthHeader }) => {
  return (
    <>
      <InputGroup className="mb-3">
        <InputGroup.Text id="basic-addon1">x-api-key</InputGroup.Text>
        <FormControl
          placeholder="key value"
          aria-label="x-api-key"
          aria-describedby="basic-addon1"
          onChange={(e) => setAuthHeader({key: 'x-api-key', value: e.target.value})}
        />
      </InputGroup>
    </>
  );
};

export default SecurityBtn;