/* eslint-disable no-unused-vars*/

import { useState, useEffect } from 'react';

import Parameter from '../components/Parameter';
import Request from '../components/Request';
import Response from '../components/Response';
import ServerResponseController from '../components/ServerResponseController';
import SecurityBtn from '../utils/SecurityBtn';

// import 'bootstrap/dist/css/bootstrap.min.css';

import { Stack, Grid, Item, Accordion, AccordionDetails, AccordionSummary, Typography, Chip, FormControl, InputLabel, NativeSelect } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import ThemeContext, { theme } from '../global/theme-context';

/*============ get all tags ============*/
function getTags(docValue) {
  const { paths } = docValue;
  const pathsArr = Object.keys(paths); //pathsarray  let tagArr = [];
  let tagArr = [];
  for (let i = 0; i < pathsArr.length; i++) {
    // console.log(pathsArr[i]); //paths

    const requestsArr = Object.keys(paths[pathsArr[i]]); //requestsarray

    for (let j = 0; j < requestsArr.length; j++) {
      // console.log(requestsArr[j]); //requests

      const { tags } = paths[pathsArr[i]][requestsArr[j]];
      tagArr.push(...tags);
    }
  }
  const uniqueTags = [...new Set(tagArr)];
  return uniqueTags;
}

/*============ get tagTable ============*/
function getTagTable(docValue, TagsData) {
  const { paths } = docValue;
  const pathsArr = Object.keys(paths); //pathsarray  let tagArr = [];

  const tagTable = TagsData.reduce((acc, cur) => {
    if (!acc[cur]) {
      acc[cur] = [];
    }
    for (let i = 0; i < pathsArr.length; i++) {
      const requestsArr = Object.keys(paths[pathsArr[i]]);
      for (let j = 0; j < requestsArr.length; j++) {
        const path = pathsArr[i];
        const requestType =  requestsArr[j];
        const detailObj = paths[pathsArr[i]][requestsArr[j]];
        if (detailObj.tags.includes(cur)) {
          acc[cur].push([path, requestType, detailObj]);
        }
      }
    }
    return acc;
  }, {});
  return tagTable;
}
/*============ TODO: Refactor above since the for loop was done twice ============*/

const Viewer = ( { docValue } ) => {

  const { servers, openapi, info, consumes, produces } = docValue;
  const { version, title, description } = info;

  const uniqueTags = getTags(docValue);
  const tagTable = getTagTable(docValue, uniqueTags);

  const [ server, setServer ] = useState(servers[0].url);

  return (
    <>
      <Grid container justifyContent="center" rowSpacing={{ xs: 2, sm: 4, md: 6 }}>
        <Grid item xs={10}>
          <ViewerTitle
            servers = {servers}
            openapi = {openapi}
            consumes = {consumes}
            produces = {produces}
            version = {version}
            title = {title}
            description = {description}
            setServer = {setServer}
          />
        </Grid>
        <Grid item xs={10}>
          <ViewerBody
            uniqueTags = {uniqueTags}
            tagTable = {tagTable}
            server = {server}
          />
        </Grid>
      </Grid>
    </>
  );
};

const ViewerTitle = ( { servers, openapi, consumes, produces, version, title, description, setServer } ) => {
  return (
    <>
      <Grid container alignItems="center" spacing={2}>
        <Grid item xs={6}>
          <h1>{title}</h1>
          <Chip label={`Version ${version}`} color="primary" variant="outlined"></Chip><Chip label={`OAS ${openapi}`} color="success" variant="outlined"></Chip>
        </Grid>
        <Grid item xs={6}>
          {description}
        </Grid>
      </Grid>
      <FormControl fullWidth>
        <InputLabel variant="standard" htmlFor="uncontrolled-native">
          Server
        </InputLabel>
        <NativeSelect
          defaultValue={servers[0].url}
          inputProps={{
            name: 'url',
            id: 'uncontrolled-native',
          }}
          onChange={e => setServer(e.target.value.split(' - ')[0])}
        >
          {servers.map(server => <option key={server.url}>{`${server.url} - ${server.description}`}</option>)}
          <option>mock-server</option>
        </NativeSelect>
      </FormControl>
    </>
  );
};

const ViewerBody = ( { uniqueTags, tagTable, server } ) => {
  return (
    <>
      <Grid container justifyContent="center" rowSpacing={{ xs: 1, sm: 2, md: 3 }}>
      {uniqueTags.map(tag =>
        <Grid item xs={12} key={tag}>
          <Tag
            tagName={tag}
            detailArr = {tagTable[tag]}
            server = {server}
          />
        </Grid>
      )}
      </Grid>
    </>
  );
};

const Tag = ({ tagName, detailArr, server }) => {

  const [expanded, setExpanded] = useState(false);
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography sx={{ width: '33%', flexShrink: 0 }}>
            {tagName}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          {detailArr.map(detail =>
              <Body
                detail = {detail}
                server = {server}
                key = {detail[1] + ' ' + detail[0]}
              />
            )}
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

const Body = ({ detail, server }) => {

  const path = detail[0];

  const requestType = detail[1];
  const {
    summary,
    produces,
    security,
    parameters,
    requestBody,
    responses,
  } = detail[2];

  function applyColor(requestType) {
    switch (requestType) {
      case 'get':
        setColor('primary');
        break;
      case 'post':
        setColor('success');
        break;
      case 'put':
        setColor('warning');
        break;
      case 'delete':
        setColor('error');
        break;
      default:
        break;
    }
  }

  const [color, setColor] = useState('primary');

  useEffect(() => {
    applyColor(requestType);
  }, [requestType]);

  return (
    <div>
      <Title
        color = {color}
        path = {path}
        requestType = {requestType}
        summary = {summary}
        security = {security}
        parameters = {parameters}
        requestBody = {requestBody}
        responses = {responses}
        server = {server}
      />
    </div>
  );
};

const Title = ( { color, path, requestType, summary, security, parameters, requestBody, responses, server } ) => {

  function getInitAuthType(security) {
    if (!security) return '';
    return Object.keys(security[0])[0];
  }

  const initAuthType = getInitAuthType(security);

  const [authType, setAuthType] = useState(initAuthType);
  const [authHeader, setAuthHeader] = useState({key: 'Auth', value: {username: '', password: ''}});

  function getInitReqBody(requestBody) {
    if (!requestBody) return '';

    const schemaObj = requestBody.content[Object.keys(requestBody.content)[0]];
    if (!schemaObj.examples && !schemaObj.example) return '';

    if (schemaObj.examples) {
      return schemaObj.examples[Object.keys(schemaObj.examples)[0]].value;
    } else if (schemaObj.example) {
      return schemaObj.example;
    }
  }

  function getInitContentType(requestBody) {
    if (!requestBody || !requestBody.content) return '';
    return Object.keys(requestBody.content)[0];
  }

  const initReqBody = getInitReqBody(requestBody);
  const initContentType = getInitContentType(requestBody);

  const [ reqBody, setReqBody ] = useState(initReqBody);
  const [ paramInfos, setParamInfos ] = useState(parameters);
  const [ reqContentType, setReqContentType ] = useState(initContentType);

  const [expanded, setExpanded] = useState(false);
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="main-content"
        >
            <Stack direction="row" spacing={3}>
              <Chip label={requestType.toUpperCase()} color={color} variant="outlined" />
              <div className="border ms-auto"><h5>{path}</h5></div>
              <div><h6>{summary}</h6></div>
            </Stack>
        </AccordionSummary>
        <AccordionDetails>
          <ServerResponseController
            color = {color}
            server = {server}
            path = {path}
            paramInfos = {paramInfos}
            body = {reqBody}
            type = {requestType}
            authHeader = {authHeader}
            reqContentType = {reqContentType}
            responses = {responses}
          />
          <SecurityBtn
            securities = {security}
            authType = {authType}
            setAuthType = {setAuthType}
            setAuthHeader = {setAuthHeader}
          />
          <Stack spacing={3}>
            <ThemeContext.Provider value={theme}>
              <Parameter
                color = {color}
                parameters = {parameters}
                setParamInfos = {setParamInfos}
              />
              <Request
                color = {color}
                requestBody = {requestBody}
                setReqBody = {setReqBody}
                setReqContentType = {setReqContentType}
              />
              <Response
                color = {color}
                responses = {responses}
              />
            </ThemeContext.Provider>
          </Stack>
        </AccordionDetails>
      </Accordion>
    </div>
    );
};

export default Viewer;
