import { createContext } from 'react';

export const theme = {
  palette: {
    primary: {
      main: '#ADD8E6',
      bright: '#5D76D9',
    },
    success: {
      main: '#90ee90',
      bright: '#52D968',
    },
    warning: {
      main: '#ffffe0',
      bright: '#D9B348',
    },
    error: {
      main: '#FFCCCB',
      bright: '#D97389',
    },
  },
};

const ThemeContext = createContext(theme);

export default ThemeContext;