import { useState, useEffect, useContext, createContext, Fragment } from 'react';

import { Paper, Stack, Grid, Box, TextField, Accordion, AccordionDetails, AccordionSummary, Typography, Container, Chip, ListItem, ListItemText, Card, CardContent, CardActions, Button, Select, MenuItem, FormControl, InputLabel,  } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import AddCircleOutlineOutlinedIcon from '@mui/icons-material/AddCircleOutlineOutlined';
import LogoutIcon from '@mui/icons-material/Logout';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';

import { green, orange, red } from '@mui/material/colors';

import { fetchUserDocById, fetchUsersByDocId, addUserToDoc, changeUserRoleInDoc, deleteUserFromDoc } from '../utils/fetchData';
import { SetDocData, CurrentUserRole } from '../containers/App';

const DocId = createContext(null);
const SetUsers = createContext(null);

const Document = ({ docValue, docId }) => {

  const token = localStorage.getItem('token');
  const setDocData = useContext(SetDocData);

  const [users, setUsers] = useState(null);

  useEffect(() => {
    fetchUserDocById(token, docId, setDocData);
    fetchUsersByDocId(docId, setUsers);
  }, []);

  return (
    docValue && users
    ?
    <>
      <DocId.Provider value={docId}>
      <SetUsers.Provider value={setUsers}>
        <Container>
          <Stack marginTop={5}>
            <DocContainer docValue={JSON.parse(docValue)}/>
            <DocUsers users = {users}/>
          </Stack>
        </Container>
      </SetUsers.Provider>
      </DocId.Provider>
    </>
    :
    null
  );
};

const DocContainer = ({ docValue }) => {

  const { servers, openapi, info } = docValue;
  const { version, title, description } = info;

  const [expanded, setExpanded] = useState(false);
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return(
    <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography variant='h6' sx={{ width: '33%', flexShrink: 0 }}>{title}</Typography>
        <Typography sx={{ color: 'text.secondary' }}>{description}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Grid container spacing={2}>
          <Grid item xs={10}>
            <NewUserController/>
            {
              servers.map((server, index) => {
                return (
                  <ListItem key={index}>
                    <ListItemText primary={server.description} secondary={server.url} />
                  </ListItem>
                );
              })
            }
          </Grid>
          <Grid item xs={2}>
            <Stack spacing={1}>
              <Chip label={`Ver ${version}`} color="primary" variant="outlined" />
              <Chip label={`OAS ${openapi}`} color="success" variant="outlined" />
            </Stack>
          </Grid>
        </Grid>
      </AccordionDetails>
    </Accordion>
  );
};

const NewUserController = () => {
  const docId = useContext(DocId);
  const currentUserRole = useContext(CurrentUserRole);
  const setUsers = useContext(SetUsers);

  const [role, setRole] = useState('viewer');
  const [email, setEmail] = useState('');
  async function addNewUser() {
    const result = await addUserToDoc(email, role, docId);
    if (result.err) {
      return;
    }
    setUsers(users => {
      const newUsers = {...users};
      newUsers[`${role}s`].push(result.data.user);
      return newUsers;
    });
  }

  return(
    <>
    {
      currentUserRole === 'viewer' ? null :
      <>
      <Stack direction="row" spacing={2}>
        <TextField id="new-user-email" size="small" label="email" variant="outlined" value={email} onChange={(e) => setEmail(e.target.value)} />
        <Select labelId="new-user-selector" id="new-user-selector" size="small" value={role} label="Role" onChange={(e) => setRole(e.target.value)}>
          {currentUserRole === 'owner' ? <MenuItem value={'owner'}>owner</MenuItem> : null}
          <MenuItem value={'editor'}>editor</MenuItem>
          <MenuItem value={'viewer'}>viewer</MenuItem>
        </Select>
        <Button
          variant="outlined"
          sx={{ color: green[500] }}
          endIcon={<AddCircleOutlineOutlinedIcon />}
          onClick={addNewUser}
        >
          New User
        </Button>
      </Stack>
      </>
    }
    </>
  );
};

const DocUsers = ({ users }) => {
  const { owner, editors, viewers } = users;
  return (
    <>
      <Grid container spacing={4} marginTop={2}>
      <Grid item xs={3}>
        <Paper elevation={20} >
          <DocUserCard user={owner} role='owner' />
        </Paper>
      </Grid>
      {
        editors.map((editor, index) => {
          return <Grid key={index}  item xs={3}><Paper elevation={5} ><DocUserCard user={editor} role='editor' /></Paper></Grid>;
        })
      }
        {
          viewers.map((viewer, index) => {
            return <Grid key={index} item xs={3}><Paper elevation={1} ><DocUserCard user={viewer} role='viewer'/></Paper></Grid>;
          })
        }
      </Grid>
    </>
  );
};

const DocUserCard = ({ user, role }) => {
  const { id, name, email } = user;

  return (
    <Card variant="outlined">
      <Fragment>
        <CardContent>
          <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            {role}
          </Typography>
          <Typography variant="h5" component="div">
            {name}
          </Typography>
          <Typography sx={{ mb: 1.5 }} color="text.secondary">
            {email}
          </Typography>
          <Typography variant="body2">
            well meaning and kindly.
            <br />
            {'"a benevolent smile"'}
          </Typography>
        </CardContent>
        <CardActions>
          <UserRoleHandlerContainer initRole={role} userId={id} userName={name} userEmail={email} />
        </CardActions>
      </Fragment>
    </Card>
  );
};

const UserRoleHandlerContainer = ({ initRole, userId, userName, userEmail }) => {
  const docId = useContext(DocId);
  const currentUserRole = useContext(CurrentUserRole);
  const setUsers = useContext(SetUsers);

  const [role, setRole] = useState(initRole);

  async function changeUserRole() {
    const result = await changeUserRoleInDoc(userId, docId, role);
    if (result.err) {
      return;
    }
    setUsers(users => ({ ...users, [`${initRole}s`]: users[`${initRole}s`].filter(user => user.id !== userId) }));
    setUsers(users => {
      const newUsers = {...users};
      newUsers[`${role}s`].push({
        email: userEmail,
        name: userName,
        id: userId,
      });
      return newUsers;
    });
  }

  async function removeUser() {
    const result = await deleteUserFromDoc(userId, docId);
    if (result.err) {
      return;
    }
    setUsers(users => ({ ...users, [`${role}s`]: users[`${role}s`].filter(user => user.id !== userId) }));
  }

  switch (currentUserRole) {
    case 'owner':
      return (
        <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
        <InputLabel id="change-user-role-label">Role</InputLabel>
          <Select
            labelId="change-user-role-select-small"
            id="change-user-role-select-small"
            value={role}
            label="Role"
            onChange={(e) => setRole(e.target.value)}
          >
            <MenuItem value={'owner'}>owner</MenuItem>
            <MenuItem value={'editor'}>editor</MenuItem>
            <MenuItem value={'viewer'}>viewer</MenuItem>
          </Select>
          <Button color="inherit" variant="outlined" onClick={changeUserRole}>Change Role</Button>
          <Button color="error" variant="outlined" onClick={removeUser} >Remove This User</Button>
        </FormControl>
      );
    case 'editor':
      return (
        <>
          {
            initRole === 'owner'
            ?
            null
            :
            <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
            <InputLabel id="change-user-role-label">Role</InputLabel>
              <Select
                labelId="change-user-role-select-small"
                id="change-user-role-select-small"
                value={role}
                label="Role"
                onChange={(e) => setRole(e.target.value)}
              >
                <MenuItem value={'editor'}>editor</MenuItem>
                <MenuItem value={'viewer'}>viewer</MenuItem>
              </Select>
              <Button variant="contained" onClick={changeUserRole}>Change Role</Button>
            </FormControl>
          }
        </>
      );
    case 'viewer':
      return null;
    default:
      return null;
  }
};

export default Document;