import { useState, useContext } from 'react';

import { Button, Table } from 'react-bootstrap';
import { FormControl, InputLabel, NativeSelect } from '@mui/material';

import { ResponseInput } from '../utils/Input';
import { fetchServerResponse, fetchMockServerResponse } from '../utils/fetchData';
import { DocumentId } from '../containers/App';

const ServerResponseController = ({ color, server, path, paramInfos, body, type, authHeader, reqContentType, responses }) => {
  if (server === 'mock-server' && responses) {
    return (
      <>
        <MockServer
          color = {color}
          server = {server}
          path = {path}
          paramInfos = {paramInfos}
          body = {body}
          type = {type}
          authHeader = {authHeader}
          reqContentType = {reqContentType}
          responses = {responses}
        />
      </>
    );
  } else {
    return (
      <>
        <UserServer
          color = {color}
          server = {server}
          path = {path}
          paramInfos = {paramInfos}
          body = {body}
          type = {type}
          authHeader = {authHeader}
          reqContentType = {reqContentType}
        />
      </>
    );
  }
};

const MockServer = ({ color, server, path, paramInfos, body, type, authHeader, reqContentType, responses }) => {
  const docId = useContext(DocumentId);

  const statusCodes = Object.keys(responses);
  const firstStatusCodeContentTypes = Object.keys(responses[statusCodes[0]].content);
  const firstStatusCodeResponseCategories = Object.keys(responses[statusCodes[0]].content[firstStatusCodeContentTypes[0]]).filter(category => category !== 'schema');

  let firstResCateExamples = [];
  const examples = responses[statusCodes[0]].content[firstStatusCodeContentTypes[0]].examples;
  if (examples) {
    firstResCateExamples = Object.keys(examples);
  }

  const [statusCodesOptions, setStatusCodesOptions] = useState(statusCodes);
  const [contentTypesOptions, setContentTypesOptions] = useState(firstStatusCodeContentTypes);
  const [responseCategoriesOptions, setResponseCategoriesOptions] = useState(firstStatusCodeResponseCategories);
  const [examplesOptions, setExamplesOptions] = useState(firstResCateExamples);

  const [statusCode, setStatusCode] = useState(statusCodes[0]);
  const [contentType, setContentType] = useState(firstStatusCodeContentTypes[0]);
  const [responseCategory, setResponseCategory] = useState(firstStatusCodeResponseCategories[0]);
  const [examplesName, setExamplesName] = useState(firstResCateExamples[0]);

  if (paramInfos) {
    const pathParams = paramInfos.filter(param => param.in === 'path');
    const queryParams = paramInfos.filter(param => param.in === 'query');
    pathParams.forEach(param => {
      path = path.replace(`{${param.name}}`, param.value);
    });
    queryParams.forEach(param => {
      path = `${path}?${param.name}=${param.value}`;
    });
  }
  const url = server + path;

  const [isMockUrlShown, setMockUrl] = useState(false);
  const [isServerResShown, setServerRes] = useState(false);

  const [mockUrl, setMockUrlValue] = useState('');
  const [resHeaders, setResHeaders] = useState({});
  const [resStatus, setResStatus] = useState('');
  const [resData, setResData] = useState('');

  function showMockUrl(mockUrl) {
    setMockUrl(true);
    setMockUrlValue(mockUrl);
  }

  return (
    <>
      <MockServerOptions
        color = {color}
        responses = {responses}
        statusCode = {statusCode}
        contentType = {contentType}
        statusCodesOptions = {statusCodesOptions}
        contentTypesOptions = {contentTypesOptions}
        setContentTypesOptions = {setContentTypesOptions}
        responseCategoriesOptions = {responseCategoriesOptions}
        setResponseCategoriesOptions = {setResponseCategoriesOptions}
        examplesOptions = {examplesOptions}
        setExamplesOptions = {setExamplesOptions}
        setStatusCode = {setStatusCode}
        setContentType = {setContentType}
        setResponseCategory = {setResponseCategory}
        setExamplesName = {setExamplesName}
      />
      <div className="d-grid gap-2">
        <Button
          variant="secondary"
          onClick={() => fetchMockServerResponse(docId, path, type, statusCode, contentType, responseCategory, examplesName, setResHeaders, setResStatus, setResData, setServerRes, showMockUrl)}
        >
        Fetch Mock Response
        </Button>
      </div>
      <>
      {
        isMockUrlShown
        ?
        mockUrl
        :
        null
      }
      </>
      {
        isServerResShown
        ?
        <ServerRes
          color = {color}
          method = {type}
          reqUrl = {url}
          reqHeaders = {{'content-type': 'application/json'}} //TODO:
          reqBody = {body}
          resHeaders = {resHeaders}
          resStatus = {resStatus}
          resData = {resData}
        />
        :
        null
      }
    </>
  );
};

const MockServerOptions = ({ color, responses, statusCode, contentType, statusCodesOptions, contentTypesOptions, setContentTypesOptions, responseCategoriesOptions, setResponseCategoriesOptions, examplesOptions, setExamplesOptions, setStatusCode, setContentType, setResponseCategory, setExamplesName }) => {
  function handleStatusCodeChange(event) {
    const currentStatusCode = event.target.value;
    const availableContentTypes = Object.keys(responses[currentStatusCode].content);
    const availableCategories = Object.keys(responses[currentStatusCode].content[availableContentTypes[0]]).filter(category => category !== 'schema');
    let availableExamples = [];
    if (responses[currentStatusCode].content[availableContentTypes[0]].examples) {
      availableExamples = Object.keys(responses[currentStatusCode].content[availableContentTypes[0]].examples);
    }
    setContentTypesOptions(availableContentTypes);
    setResponseCategoriesOptions(availableCategories);
    setExamplesOptions(availableExamples);

    setStatusCode(currentStatusCode);
    setContentType(availableContentTypes[0]);
    setResponseCategory(availableCategories[0]);
    setExamplesName(availableExamples[0]);
  }

  function handleContentTypeChange(event) {
    const currentContentType = event.target.value;
    const availableCategories = Object.keys(responses[statusCode].content[currentContentType]).filter(category => category !== 'schema');
    let availableExamples = [];
    if (responses[statusCode].content[currentContentType].examples) {
      availableExamples = Object.keys(responses[statusCode].content[currentContentType].examples);
    }
    setResponseCategoriesOptions(availableCategories);
    setExamplesOptions(availableExamples);

    setContentType(currentContentType);
    setResponseCategory(availableCategories[0]);
    setExamplesName(availableExamples[0]);
  }

  function handleResponseCategoryChange(event) {
    const currentResponseCategory = event.target.value;
    let availableExamples = [];
    if (responses[statusCode].content[contentType].examples) {
      availableExamples = Object.keys(responses[statusCode].content[contentType].examples);
    }
    setExamplesOptions(availableExamples);

    setResponseCategory(currentResponseCategory);
    setExamplesName(availableExamples[0]);
  }

  function handleExamplesNameChange(event) {
    const currentExamplesName = event.target.value;
    setExamplesName(currentExamplesName);
  }

  return (
    <>
      <FormControl>
        <InputLabel variant="standard" htmlFor="status-code">
          Status Code
        </InputLabel>
        <NativeSelect
          defaultValue={statusCodesOptions[0]}
          inputProps={{
            name: 'status-code',
            id: 'status-code',
          }}
          onChange={handleStatusCodeChange}
        >
          {statusCodesOptions.map(statusCode => <option key={statusCode}>{statusCode}</option>)}
        </NativeSelect>
      </FormControl>
      <FormControl>
        <InputLabel variant="standard" htmlFor="content-type">
            Content Type
          </InputLabel>
          <NativeSelect
            defaultValue={contentTypesOptions[0]}
            inputProps={{
              name: 'content-type',
              id: 'content-type',
            }}
            onChange={handleContentTypeChange}
          >
            {contentTypesOptions.map(contentType => <option key={contentType}>{contentType}</option>)}
          </NativeSelect>
      </FormControl>
      <FormControl>
        <InputLabel variant="standard" htmlFor="response-category">
            Response Category
          </InputLabel>
          <NativeSelect
            defaultValue={responseCategoriesOptions[0]}
            inputProps={{
              name: 'response-category',
              id: 'response-category',
            }}
            onChange={handleResponseCategoryChange}
          >
            {responseCategoriesOptions.map(responseCategory => <option key={responseCategory}>{responseCategory}</option>)}
          </NativeSelect>
      </FormControl>
      {
        examplesOptions.length <= 0
        ?
        null
        :
        <FormControl>
          <InputLabel variant="standard" htmlFor="examples-name">
              Examples Name
            </InputLabel>
            <NativeSelect
              defaultValue={examplesOptions[0]}
              inputProps={{
                name: 'examples-name',
                id: 'examples-name',
              }}
              onChange={handleExamplesNameChange}
            >
              {examplesOptions.map(example => <option key={example}>{example}</option>)}
            </NativeSelect>
        </FormControl>
      }
    </>
  );
};

const UserServer = ({ color, server, path, paramInfos, body, type, authHeader, reqContentType }) => {
  if (paramInfos) {
    const pathParams = paramInfos.filter(param => param.in === 'path');
    const queryParams = paramInfos.filter(param => param.in === 'query');
    pathParams.forEach(param => {
      path = path.replace(`{${param.name}}`, param.value);
    });
    queryParams.forEach(param => {
      path = `${path}?${param.name}=${param.value}`;
    });
  }
  const url = server + path;

  const [isServerResShown, setServerRes] = useState(false);

  const [resHeaders, setResHeaders] = useState({});
  const [resStatus, setResStatus] = useState('');
  const [resData, setResData] = useState('');

  return (
    <>
      <div className="d-grid gap-2">
        <Button
          variant="secondary"
          onClick={() => fetchServerResponse(reqContentType, authHeader, type, url, body, setResHeaders, setResStatus, setResData, setServerRes)}
        >
        Execute
        </Button>
      </div>
      {
        isServerResShown
        ?
        <ServerRes
          color = {color}
          method = {type}
          reqUrl = {url}
          reqHeaders = {{'content-type': 'application/json'}} //TODO:
          reqBody = {body}
          resHeaders = {resHeaders}
          resStatus = {resStatus}
          resData = {resData}
        />
        :
        null
      }
    </>
  );
};

const ServerRes = ({ color, method, reqUrl, reqHeaders, reqBody, resHeaders, resStatus, resData }) => {
  return (
    <>
      {/*TODO: <div className={`bg-light text-${color}`}><h3>Server Response</h3></div> */}
      <Table responsive>
        <thead>
          <tr>
            <th>Code</th>
            <th>Body</th>
            <th>Headers</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{resStatus}</td>
            <td><ResponseInput
              exampleValue = {resData}
            /></td>
            <td><ResponseInput
              exampleValue = {resHeaders}
            /></td>
          </tr>
        </tbody>
      </Table>
      {/*TODO: <div className={`bg-light text-${color}`}><h3>Original Request</h3></div> */}
      <Table responsive>
        <thead>
          <tr>
            <th>Method</th>
            <th>Url</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{method}</td>
            <td>{reqUrl}</td>
          </tr>
        </tbody>
      </Table>
      <Table responsive>
        <thead>
          <tr>
            <th>Body</th>
            <th>Headers</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td><ResponseInput
              exampleValue = {reqBody}
            /></td>
            <td><ResponseInput
              exampleValue = {reqHeaders}
            /></td>
          </tr>
        </tbody>
      </Table>
      </>
  );
};

export default ServerResponseController;