import { useState, useEffect, useContext, useRef } from 'react';

// import 'bootstrap/dist/css/bootstrap.min.css';
// import { /*Card, Container, Col, Row, Badge, Image */} from 'react-bootstrap';
import { Paper, Grid, Button, Chip, Card, CardContent, Typography } from '@mui/material';
import LogoutIcon from '@mui/icons-material/Logout';
import PreviewIcon from '@mui/icons-material/Preview';
import EditIcon from '@mui/icons-material/Edit';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import ManageSearchIcon from '@mui/icons-material/ManageSearch';

import { fetchUserData, fetchUserDocById, deleteUserDocById, userLeaveDoc } from '../utils/fetchData';
import { SwitchContainerFunc, SetDocData, SetDocId, SetCurrentUserRole } from '../containers/App';

const Profile = ({ setUserName, switchToContainer }) => {

  const token = localStorage.getItem('token');

  const [userData, setUserData] = useState(null);

  useEffect(() => {
    fetchUserData(token, setUserData, setUserName, switchToContainer);
    if (userData) {
      // const { id, provider, name, email } = userData;
      setUserName(userData.name);
    }
  }, []);

  return (
    <>
      {
        userData
        ?
        <div>
          <h1>Wellcome Back, {userData.name}</h1>
          {
            userData.docs.length > 0
            ?
            <Grid container spacing={4} paddingLeft={20} paddingRight={20} marginTop={5} marginBottom={5}>
                {
                  userData.docs.map(doc => {
                    return <Grid item xs={0} key={doc.id} ><DocInfo doc={doc} removeDoc={setUserData} /></Grid>;
                  })
                }
            </Grid>
            :
            <h1>Seems like you got no document. Create One ?</h1>
          }
        </div>
        :
        null
      }
    </>
  );
};

const DocInfo = ({ doc, removeDoc }) => {

  const { id, role, openapi } = doc;
  const { version, title, description } = doc.info;

  let elevation = 1;
  if (role ==='owner') {
    elevation = 20;
  } else if (role === 'editor') {
    elevation = 5;
  }

  return (
    <>
      <Paper elevation={elevation}>
        <Card border="light" style={{ width: '18rem' }}>
          <CardContent>
            <Grid container spacing={0}>
              <Grid item xs={10}>
                <Typography variant="h5" component="div">
                  {title ? title : 'Not Found'}
                </Typography>
              </Grid>
              <Grid item xs={2}>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                  {role}
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography sx={{ mb: 1.5 }} color="text.secondary">
                  {description}
                </Typography>
              </Grid>
              <Grid item xs={4}>
                <Chip label={`Ver ${version}`} color="primary" variant="outlined" size="small"></Chip>
              </Grid>
              <Grid item xs={4}>
                <Chip label={`OAS ${openapi}`} color="success" variant="outlined" size="small"></Chip>
              </Grid>
              <Grid item xs={12}>
                <DocController role={role} id={id} removeDoc={removeDoc}/>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Paper>
    </>
  );
};

const DocController = ({ role, id, removeDoc }) => {

  const token = localStorage.getItem('token');

  const setDocData = useContext(SetDocData);
  const switchToContainer = useContext(SwitchContainerFunc);
  const setDocId = useContext(SetDocId);
  const setCurrentUserRole = useContext(SetCurrentUserRole);

  function viewDoc() {
    fetchUserDocById(token, id, setDocData);
    setDocId(id);
    switchToContainer('viewer');
  }

  function manageDoc() {
    setDocId(id);
    setCurrentUserRole(role);
    switchToContainer('document');
  }

  async function deleteData() {
    const result = await deleteUserDocById(token, id);
    if (result.err) {
      return;
    }
    removeDoc((prev) => {
      const newUser = { ...prev };
      const newDoc = newUser.docs.filter(doc => doc.id !== id);
      newUser.docs = newDoc;
      return newUser;
    });
  }

  async function leaveDoc() {
    if (role === 'owner') {
      alert('You are the owner of this document. You can not leave it.');
      return;
    }
    const result = await userLeaveDoc(id);
    if (result.err) {
      return;
    }
    removeDoc((prev) => {
      const newUser = { ...prev };
      const newDoc = newUser.docs.filter(doc => doc.id !== id);
      newUser.docs = newDoc;
      return newUser;
    });
  }

  function switchToUpdater() {
    setDocId(id);
    switchToContainer('updater');
  }

  switch (role) {
    case 'owner':
      return (
        <>
        <PreviewIcon onClick={viewDoc} sx={{ fontSize: 40 }}/>
        <EditIcon onClick={switchToUpdater} sx={{ fontSize: 40 }}/>
        <DeleteForeverIcon onClick={deleteData} sx={{ fontSize: 40 }}/><br/>
        <Button color='inherit' variant="outlined" onClick={manageDoc} endIcon={<ManageSearchIcon />}>Collaborators</Button>
        <Button color='error' variant="outlined" onClick={leaveDoc}  endIcon={<LogoutIcon />}>Leave Doc</Button>
        </>
      );
    case 'editor':
      return (
        <>
        <PreviewIcon onClick={viewDoc} sx={{ fontSize: 40 }}/>
        <EditIcon onClick={switchToUpdater} sx={{ fontSize: 40 }}/><br/>
        <Button color='inherit' variant="outlined" onClick={manageDoc} endIcon={<ManageSearchIcon />}>Collaborators</Button>
        <Button color='error' variant="outlined" onClick={leaveDoc}  endIcon={<LogoutIcon />}>Leave Doc</Button>
        </>
      );
    case 'viewer':
      return (
        <>
        <PreviewIcon onClick={viewDoc} sx={{ fontSize: 40 }}/><br />
        <Button color='inherit' variant="outlined" onClick={manageDoc} endIcon={<ManageSearchIcon />}>Collaborators</Button>
        <Button color='error' variant="outlined" onClick={leaveDoc} endIcon={<LogoutIcon />}>Leave Doc</Button>
        </>
      );
    default:
      return null;
  }
};

export default Profile;