/* eslint-disable no-unused-vars*/

import { Button, Grid } from '@mui/material';

import { EditorInput } from '../utils/Input';
import { addUserDoc } from '../utils/fetchData';

const Editor = ( { initDoc, docValue, setDoc, switchToContainer } ) => {
  const token = localStorage.getItem('token');

  function addNewDoc() {
    addUserDoc(token, JSON.parse(docValue));
    switchToContainer('profile');
  }

  return (
    <>
      <Grid container spacing={2} justifyContent="center">
        <Grid item xs={10}>
          <Button href="#viewer" variant="contained" color="primary" onClick={() => switchToContainer('viewer')}>View</Button>
          <Button variant="contained" color="success" onClick={() => addNewDoc()}>Save</Button>
        </Grid>
        <Grid item xs={10}>
        <EditorInput exampleValue = { initDoc } setDoc = { setDoc }/>
        </Grid>
      </Grid>
    </>
  );
};

export default Editor;