import { useContext } from 'react';
import { Box, ThemeProvider } from '@mui/system';

import { ReqContent } from './Schema';

import ThemeContext from '../global/theme-context';

const Request = ({ requestBody, color, setReqBody, setReqContentType }) => {

  const theme = useContext(ThemeContext);

  return (
    <div>
      <h4>Requests</h4>
      <ThemeProvider theme={theme}>
        <Box
          sx={{
            bgcolor: `${color}.main`,
            '&:hover': {
              backgroundColor: `${color}.bright`,
              opacity: [0.9, 0.8, 0.7],
            },
          }}
        >
        {
          requestBody
          ?
          <ReqBody
            color = {color}
            requestBody = {requestBody}
            setReqBody = {setReqBody}
            setReqContentType = {setReqContentType}
          />
          :
          'no request body'
        }
        </Box>
      </ThemeProvider>
    </div>
  );
};

const ReqBody = ({ requestBody, color, setReqBody, setReqContentType }) => {

  const { description, required, content } = requestBody;

  return (
      <div>
        <div>{required ? 'required' : null}</div>
        <div>{description}</div>
        <ReqContent
          color = {color}
          content = {content}
          setReqBody = {setReqBody}
          setReqContentType = {setReqContentType}
        />
      </div>
  );
};

export default Request;