/* eslint-disable no-unused-vars*/
import { useState, useEffect } from 'react';

import { Button, Grid } from '@mui/material';

import { UpdaterInput } from '../utils/Input';
import { fetchUserDocById, updateUserDocById } from '../utils/fetchData';

const Updater = ( { docId, docValue, setDoc, switchToContainer } ) => {
  const token = localStorage.getItem('token');

  const [initDoc, setInitDoc] = useState('test');

  useEffect(() => {
    fetchUserDocById(token, docId, setDoc).then(data => setInitDoc(data));
  }, []);

  function updateDoc() {
    updateUserDocById(token, docId, JSON.parse(docValue));
    switchToContainer('profile');
  }

  return (
    <>
      <Grid container spacing={2} justifyContent="center">
        <Grid item xs={10}>
          <Button href="#viewer" variant="contained" color="primary" onClick={() => switchToContainer('viewer')}>View</Button>
          <Button variant="contained" color="success" onClick={updateDoc}>Save</Button>
        </Grid>
        <Grid item xs={10}>
        <UpdaterInput exampleValue = { initDoc } setDoc = { setDoc }/>
        </Grid>
      </Grid>
    </>
  );
};

export default Updater;