import { useContext } from 'react';
import { Box, ThemeProvider } from '@mui/system';

import Table from 'react-bootstrap/Table';

import ThemeContext from '../global/theme-context';
import { ResContent } from './Schema';

const Response = ({ responses, color }) => {

  const theme = useContext(ThemeContext);

  return (
    <div>
      <h4>Responses</h4>
      <ThemeProvider theme={theme}>
        <Box
          sx={{
            bgcolor: `${color}.main`,
            '&:hover': {
              backgroundColor: `${color}.bright`,
              opacity: [0.9, 0.8, 0.7],
            },
          }}
        >
        {
          responses
          ?
          <ResBody
            color = {color}
            responses = {responses}
          />
          :
          'no response'
        }
        </Box>
      </ThemeProvider>
  </div>
  );
};

const ResBody = ({ responses, color }) => {

  const statusCodes = Object.keys(responses);

  return (
    <div>
      {
        <Table responsive>
        <thead>
          <tr>
            <th>Name</th>
            <th>Description</th>
          </tr>
        </thead>
        {statusCodes.map((statusCode) => {
          return (
            <StatusBody
              color = {color}
              statusCode = {statusCode}
              statusData = {responses[statusCode]}
              key = {statusCode}
            />
          );
        })}
        </Table>
      }
    </div>
  );
};

const StatusBody = ( { statusCode, statusData, color } ) => {

  const { description, content } = statusData;

  return (
    <tbody>
      <tr>
        <td>
          {statusCode}
        </td>
        <td>
          {description}
          <ResContent
            color = {color}
            content = {content}
          />
        </td>
      </tr>
    </tbody>
  );
};

export default Response;